  src: url('./fonts//SFProDisplay-Medium.woff2') format('woff2'),
    url('./fonts//SFProDisplay-Medium.woff') format('woff');
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: 'SFProDisplay-Semibold', sans-serif;
